<div><label for="termFunction">  </label><select class="form-control form-select my-1" id="termFunction" required @input="setFunctionAddableTerm({index:$event.target.value,termFunctions:$store.state.type.termFunctions})"><option v-for="(func,index) in $store.state.type.termFunctions" :value="index">{{ func.name }}</option></select><div class="row" v-if="[&quot;&quot;,&quot;&quot;].includes($store.state.addableTerm.termFunction.name)"><span>:</span><div class="col"><input class="form-control" type="number" required @input="setLeftAddableTerm($event.target.value)"></div><div class="col" v-if="''==$store.state.addableTerm.termFunction.name"><input class="form-control" type="number" required @input="setUpperLeftAddableTerm($event.target.value)"></div><div class="col" v-if="''==$store.state.addableTerm.termFunction.name"><input class="form-control" type="number" required @input="setMeanAddableTerm($event.target.value)"></div><div class="col" v-if="''==$store.state.addableTerm.termFunction.name"><input class="form-control" type="number" required @input="setUpperRightAddableTerm($event.target.value)"></div><div class="col"><input class="form-control" type="number" required @input="setRightAddableTerm($event.target.value)"></div></div><div class="row" v-if="[&quot;&quot;,&quot;&quot;].includes($store.state.addableTerm.termFunction.name)"><div class="col"><label for="mean"></label><input class="form-control" id="mean" type="number" required @input="setMeanAddableTerm($event.target.value)"></div><div class="col"><label for="stdDev"> </label><input class="form-control" id="stdDev" type="number" required @input="setStandardDeviationAddableTerm($event.target.value)"></div></div><div v-if="'-'==$store.state.addableTerm.termFunction.name"><label for="xy">  : x1;y1 x2;y2 ...</label><input class="form-control" id="xy" type="text" required @change="setPointsAddableTerm($event.target.value)" placeholder="x1;y1 x2;y2 ..."></div></div>