<div class="col-3 bg-success bg-opacity-10 p-2" id="types"><div class="d-flex justify-content-between"><b> </b><i class="bi bi-plus-square text-success h4" data-bs-toggle="modal" data-bs-target="#modal" @click="isAddableInputVariable = true"></i></div><!--   --><ul class="list-group"><li class="list-group-item bg-success bg-opacity-10 p-1 d-flex justify-content-between" v-for="(variable, index) in $store.state.variable.inputVariables"><!--  --><div v-if="editableVariable !== variable"><div><label :for="'selectedInputVariable' + index"> </label>: {{ variable.name }}<input class="mx-1 form-check-input h4 my-0" :id="'selectedInputVariable' + index" type="radio" name="chart" @click="showChartByVariable(variable)"></div><div>: {{ variable.type.name }}</div><div>: {{ variable.value }}</div></div><!--  --><div v-if="editableVariable === variable"><label for="vName"> </label><input class="form-control" id="vName" type="text" :value="editableVariable.name" @input="setNameInputVariable({value:$event.target.value, index:index})"><label for="vType">  </label><select class="form-select" id="vType" :value="editableVariable.type.index" @change="setTypeInputVariable({type:$store.state.type.types[$event.target.value], variableIndex:index})"><option v-for="type in $store.state.type.types" :value="type.index">{{ type.name }}</option></select><label for="vValue"></label><input class="form-control" id="vValue" type="number" :value="editableVariable.value" @input="setValueInputVariable({value:$event.target.value, index:index})"></div><!--  ,    --><div class="alert alert-danger" v-if="$store.state.variable.inputVariables.length === 0">   </div><!--      --><span id="actionsInputVariables" style="min-width:52px"><i class="bi bi-dash-square text-danger h4 me-1" @click="deleteInputVariable(index)"></i><i class="bi bi-pencil-square text-info h4" @click="changeEditable(variable)"></i></span></li></ul><div class="d-flex justify-content-between"><b> </b><i class="bi bi-plus-square text-success h4" data-bs-toggle="modal" data-bs-target="#modal" @click="isAddableInputVariable = false"></i></div><!--   --><ul class="list-group"><li class="list-group-item bg-success bg-opacity-10 p-1 d-flex justify-content-between" v-for="(variable, index) in $store.state.variable.outputVariables"><div v-if="editableVariable !== variable"><div><label :for="'selectedOutputVariable' + index">: {{ variable.name }}</label><input class="mx-1 form-check-input h4 my-0" :id="'selectedOutputVariable' + index" type="radio" name="chart" @click="showChartByVariable(variable)"></div><div>: {{ variable.type.name }}</div><div> :<br>{{ variable.method.name }}</div><div> : {{ variable.def }}</div><div v-if="variable.value">: {{ variable.value }}</div></div><div v-if="editableVariable === variable"><input class="form-control" type="text" :value="editableVariable.name" @input="setNameOutputVariable({value:$event.target.value, index:index})"><select class="form-select" :value="editableVariable.type.index" @change="setTypeOutputVariable({type:$store.state.type.types[$event.target.value], index:index})"><option v-for="varType in $store.state.type.types" :key="varType.name" :value="varType.index">{{ varType.name }}</option></select><label for="vmethod"> </label><select class="form-select" id="vmethod" :value="editableVariable.method.index" @change="setMethodOutputVariable({method:methods[$event.target.value], index:index})"><option v-for="method in methods" :key="method.name" :value="method.index">{{ method.name }}</option></select><input class="form-control" type="text" :value="editableVariable.def" @input="setDefOutputVariable({value:$event.target.value, index:index})"></div><!--      --><div class="alert alert-danger" v-if="$store.state.variable.outputVariables.length === 0">  </div><!--      --><span id="actionsOutputVariables" style="min-width:52px"><i class="bi bi-dash-square text-danger h4 me-1" @click="deleteOutputVariable(index)"></i><i class="bi bi-pencil-square text-info h4" @click="changeEditable(variable)"></i></span></li></ul><!--     / --><form class="modal fade" id="modal" tabindex="-1" aria-labelledby="modalLabel" aria-hidden="true"><div class="modal-dialog"><form class="modal-content" @submit.prevent="isAddableInputVariable ? addInputVariable(addableVariable) : addOutputVariable(addableVariable)"><div class="modal-header"><h4 class="modal-title" id="modalLabel" v-if="isAddableInputVariable">  </h4><h4 class="modal-title" id="modalLabel" v-else>  </h4><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body"><div class="row"><div class="col"><label for="nameOV"> </label><input class="form-control" id="nameOV" type="text" required v-model="addableVariable.name" placeholder=""></div><div class="col" v-if="isAddableInputVariable"><label for="default"> </label><input class="form-control" id="default" required v-model="addableVariable.value" type="number"></div><div class="col" v-if="!isAddableInputVariable"><label for="default"> </label><input class="form-control" id="default" required v-model="addableVariable.def" type="number"></div></div><div class="row"><div class="col" v-if="!isAddableInputVariable"><label for="method"> </label><select class="form-select" id="method" v-model="addableVariable.method" required><option v-for="method in methods" :key="method.name" :value="method">{{ method.name }}</option></select></div><div class="col"><label for="typeOV"> </label><select class="form-select" id="typeOV" v-model="addableVariable.type" required><option v-for="varType in $store.state.type.types" :key="varType.name" :value="varType">{{ varType.name }}</option></select></div></div></div><div class="modal-footer"><button class="btn btn-success container-fluid"></button></div></form></div></form></div>