<div><label for="termFunction">  </label><select class="form-control form-select my-1" id="termFunction" required :value="$store.state.editableTerm.term.termFunction.index" @change="setFunctionEditableTerm({index:$event.target.value,termFunctions:$store.state.type.termFunctions})"><option v-for="(func,index) in $store.state.type.termFunctions" :value="index">{{ func.name }}</option></select><div class="row" v-if="[&quot;&quot;,&quot;&quot;].includes($store.state.editableTerm.term.termFunction.name)"><span>:</span><div class="col pe-0"><input class="form-control" type="number" required :value="$store.state.editableTerm.term.termFunction.left" @input="setLeftEditableTerm($event.target.value)"></div><div class="col px-0" v-if="''==$store.state.editableTerm.term.termFunction.name"><input class="form-control" type="number" required :value="$store.state.editableTerm.term.termFunction.upperLeft" @input="setUpperLeftEditableTerm($event.target.value)"></div><div class="col px-0" v-if="''==$store.state.editableTerm.term.termFunction.name"><input class="form-control" type="number" required :value="$store.state.editableTerm.term.termFunction.mean" @input="setMeanEditableTerm($event.target.value)"></div><div class="col px-0" v-if="''==$store.state.editableTerm.term.termFunction.name"><input class="form-control" type="number" required :value="$store.state.editableTerm.term.termFunction.upperRight" @input="setUpperRightEditableTerm($event.target.value)"></div><div class="col ps-0"><input class="form-control" type="number" required :value="$store.state.editableTerm.term.termFunction.right" @input="setRightEditableTerm($event.target.value)"></div></div><div class="row" v-if="[&quot;&quot;,&quot;&quot;].includes($store.state.editableTerm.term.termFunction.name)"><div class="col"><br><label for="mean"></label><input class="form-control" id="mean" type="number" required :value="$store.state.editableTerm.term.termFunction.mean" @input="setMeanEditableTerm($event.target.value)"></div><div class="col"><label for="stdDev"> </label><input class="form-control" id="stdDev" type="number" required :value="$store.state.editableTerm.term.termFunction.standardDeviation" @input="setStandardDeviationEditableTerm($event.target.value)"></div></div><div v-if="'-'==$store.state.editableTerm.term.termFunction.name"><label for="xy">  : x1;y1 x2;y2 ...</label><input class="form-control" id="xy" type="text" required :value="$store.state.editableTerm.term.termFunction.raw" @change="setPointsEditableTerm($event.target.value)" placeholder="x1;y1 x2;y2 ..."></div></div>