  export default {
    state: () => ({
      types: [
        {
					name: '',
					index:0,
          terms: [
            { name: '', termFunction: { name: '', left: '0', mean: '0', right: '2',index:0 } },
            { name: '', termFunction: { name: '', left: '1', upperLeft: '2', upperRight: '3', right: '4',index:1 } },
            { name: '', termFunction: { name: '', left: '4', mean: '5', right: '5',index:0 } },
          ],
        },
        {
					name: '',
					index:1,
          terms: [
            {
              name: '',
              termFunction: { name: '',mean:'3',standardDeviation:'0.7' ,index:2 },
						},
						{ name: '', termFunction: { name: '', left: '0', mean: '0', right: '3' ,index:0} },
            { name: '', termFunction: { name: '', left: '3', mean: '5',right:'5',index:0 } },
          ],
        },
        {
					name: '',
					index:2,
          terms: [
            { name: '', termFunction: { name: '', left: '0', mean: '0', right: '300' ,index:0} },
            { name: '', termFunction: { name: '', left: '200', upperLeft: '300', upperRight: '400',right:'600' ,index:1  } },
            { name: '', termFunction: { name: '', left: '500', mean: '1000', right: '1000',index:0 } },
          ],
        },
      ],
      termFunctions: [
        { name: '', left: '0', mean: '0', right: '0',index:0 },
        { name: '', left: '0', upperLeft: '0', upperRight: '0',right:'0' ,index:1  },
        { name: '',mean:'0', standardDeviation:'0',index:2},
        { name: '-',x:[0],y:[0],raw:'0;0',index:3},
        { name: '',mean:'0',standardDeviation:'0',index:4},
      ],
    }),
	mutations: {
		setTypes (state,types) {
			state.types=types
			},
      setTermName (state, obj) {
        state.types[obj.indexType].terms[obj.indexTerm].name=obj.name
      },
      setTypeName (state, obj) {
        state.types[obj.index].name = obj.name
      },
      setVisible (state,typeIndex) {
        state.types[typeIndex].visibility=true
      },
      setInvisible (state,typeIndex) {
        state.types[typeIndex].visibility=false
      },
		deleteTerm (state, obj) {
			state.types[obj.typeIndex].terms.splice(obj.termIndex, 1)
			this.commit('rebuildByType')
      },
      deleteType(state, index) {
        delete state.types.splice(index, 1)
      },
      setFunction(state, obj) {
        state.types[obj.typeIndex].terms[obj.termIndex].termFunction = state.termFunctions[obj.functionIndex]
      },
      addTypeName (state, name) {
				state.types.push({ name: name, terms: [] })
				alert(name+' ')
      },
      addTerm (state, obj) {
        const term = obj.term
				const typeIndex = obj.typeIndex
				console.log(JSON.stringify(obj));
        if (term && term.name && term.termFunction) {
					state.types[typeIndex].terms.push(Object.assign({}, term))
					alert(term.name + " ")
        } else {
          alert('   ')
          return
				}
				this.commit('rebuildByType')
      },
		addType (state, name) {
				state.types.push({ name: name, terms: [], index: state.types.length })
				
      },
    },
  }
